export function compareVersion(v1, v2) {
	v1 = v1.split('.')
	v2 = v2.split('.')
	const len = Math.max(v1.length, v2.length)
	while (v1.length < len) {
		v1.push('0')
	}
	while (v2.length < len) {
		v2.push('0')
	}
	for (let i = 0; i < len; i++) {
		const num1 = parseInt(v1[i], 10)
		const num2 = parseInt(v2[i], 10)

		if (num1 > num2) {
			return 1
		} else if (num1 < num2) {
			return -1
		}
	}
	return 0
}

export const ENV_TYPE = () => {
	let {SDKVersion} = uni.getSystemInfoSync()
	if(!uni.getEnv) {
		uni.getEnv = () => {





			return 'WEAPP'





			return 'uni'
		}
	}
	const type = {
		'WEAPP': '2.9.7',
		'TT': '1.78.0',
		'ALIPAY': '2.7.0',
	}[uni.getEnv()]
	return type && SDKVersion && compareVersion(SDKVersion, type) >= 1
}

