/**
 * 失败提示
 * @param {String} msg 提示消息
 * @param {Function} callback 回调函数
 */

let onoff = false;

// 处理多次点击方法
function onceClick(fn, ...arg) {
	let that = this;
	// console.log('$onceClick--Start', that.onoff);

	if (that.onoff) {
		console.log('稍等会再点, 正在处理其他东西');
	} else {
		that.onoff = true;
		console.log('$onceClick 的函数 运行');
		fn(...arg);
		setTimeout(function() {
			// console.log('$onceClick--setTimeout-before',that.onoff);
			that.onoff = false;
			// console.log('$onceClick--setTimeout-after',that.onoff);
		}, 750);
	}
	// console.log('$onceClick--End', that.onoff);
}


function errorToShow(msg = '操作失败', callback = function() {}) {
	uni.showToast({
		title: msg,
		icon: 'none',
		duration: 2000,
		mask: true
	});
	setTimeout(function() {
		callback();
	}, 2000);
}
/**
 * 交互反馈震动加音频
 * @param {Object} src
 */
function respond(src) {
	if (!src) {
		src =
			'data:audio/mpeg;base64,';
	}
	// 短震动
	uni.vibrateShort({});
	// 播放提示音
	let innerAudioContext = uni.createInnerAudioContext();
	innerAudioContext.autoplay = true;
	innerAudioContext.src = src;
	innerAudioContext.onError((res) => {
		return false;
	});
	innerAudioContext.play();
	return true;
}


/**
 * 添加cdn
 * @param {Object} srcUrl
 */
function CDN(srcUrl) {
	if (srcUrl) {
		if (srcUrl.substring(0, 9) == '/uploads/') {
			return cdnUrl + srcUrl;
		}
	}
	return srcUrl;
}



